import * as z from "zod";
import { DogSchema } from './schemas';

function validateDog(dog: unknown) {
    const result = DogSchema.safeParse(dog);

    if (!result.success) {
        // Display errors
        result.error.issues.forEach((err) => {
            console.error(`Field: ${err.path.join('.')}, Issue: ${err.message}`);
        });
        return false;
    }

    // Validation passed
    return true;
}

// Example usage:
const dogInput = {
    breed: "La", // too short
    color: "",   // too short
    price: -10,  // invalid
    weight: 200  // too high (if you set a max)
};

validateDog(dogInput);